import React, { useState } from "react";

const products = [
    { id: 1, name: "Product Alpha", className: "md:rotate-[-2deg] md:top-[0%] md:left-[5%] z-[3]" },
    { id: 2, name: "Product Beta", className: "md:rotate-[3deg] md:top-[15%] md:left-[35%] z-[2]" },
    { id: 3, name: "Product Gamma", className: "md:rotate-[-1deg] md:top-[30%] md:left-[10%] z-[1]" },
    { id: 4, name: "Product Delta", className: "hidden md:block md:rotate-[2deg] md:top-[10%] md:left-[60%] z-[4]" },
];

const Products = () => {
    const [hoveredProduct, setHoveredProduct] = useState(null);

    return (
        <section className="bg-black text-white py-12 md:py-24 px-4 md:px-8 overflow-hidden">
            <div className="max-w-7xl mx-auto">
                <h2 className="text-4xl md:text-6xl font-bold mb-10 md:mb-20 text-center tracking-tight text-white">
                    Products
                </h2>

                {/* Scattered Screenshots Container */}
                <div className="relative h-auto md:h-[700px] flex flex-col gap-12 md:block">
                    {products.map((product) => (
                        <div
                            key={product.id}
                            onMouseEnter={() => setHoveredProduct(product.id)}
                            onMouseLeave={() => setHoveredProduct(null)}
                            className={`relative md:absolute transition-all duration-500 ease-out hover:scale-105 hover:z-50 w-full md:w-[45%] max-w-[500px] mx-auto ${product.className}`}
                            style={{
                                opacity: hoveredProduct === null || hoveredProduct === product.id ? 1 : 0.3,
                            }}
                        >
                            {/* Window Frame */}
                            <div className="bg-white/90 rounded-lg shadow-2xl overflow-hidden">
                                {/* Window Title Bar */}
                                <div className="bg-gray-200 px-4 py-2 flex items-center gap-2 border-b border-gray-300">
                                    <div className="flex gap-1.5">
                                        <div className="w-3 h-3 rounded-full bg-red-500"></div>
                                        <div className="w-3 h-3 rounded-full bg-yellow-500"></div>
                                        <div className="w-3 h-3 rounded-full bg-green-500"></div>
                                    </div>
                                    <span className="text-gray-700 text-xs font-medium ml-2">{product.name}</span>
                                </div>

                                {/* Window Content - 16:9 Aspect Ratio */}
                                <div className="aspect-video bg-gradient-to-br from-gray-100 to-gray-200 flex items-center justify-center">
                                    <span className="text-gray-400 text-xl font-bold">{product.name}</span>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </section>
    );
};

export default Products;
